<template>
	<div class="van-tabbar-page box">
		<div class="header">
			<div class="energy">
				<img v-lazy="require('@/assets/energy.png')">
				<span>{{ $t('message.energyValue') }} {{ energy }}%</span>
			</div>
			<div class="language" @click="isLang = !isLang">
				<img v-lazy="require('@/assets/language.png')">
				<img v-lazy="require('@/assets/lang_icon.png')">
				<div v-show="isLang">
					<span :class="$i18n.locale == 'en-US' ? 'active' : ''" @click="changeLanguage('en-US')">English</span>
					<span :class="$i18n.locale == 'zh-CN' ? 'active' : ''" @click="changeLanguage('zh-CN')"></span>
				</div>
			</div>
			<div class="totalIncome">
				<img v-lazy="require('@/assets/totalIncome.png')">
				<span>{{ $t('message.totalIncome') }} {{ totalIncome }}ETH</span>
			</div>
			<div class="canDraw">
				<img v-lazy="require('@/assets/canDraw.png')">
				<span>{{ $t('message.earnings') }} {{ outAmount }}ETH</span>
			</div>
		</div>
		<div class="bubble" v-lazy:background-image="!withdrawable ? require('@/assets/home_logo.png') : ''">
		    <div v-if="!isBroken && isPause">
		        <div class="bubble-big" :style="random(75, 85)" v-for="(item, index) of bubble.big" :key="item + index">
		            <img v-lazy="require('@/assets/bubble.png')">
		            <span>{{ item }}</span>
		        </div>
		        <div class="bubble-middle" :style="random(80, 90)" v-for="(item, index) of bubble.middle" :key="item + index">
		            <img v-lazy="require('@/assets/bubble.png')">
		            <span>{{ item }}</span>
		        </div>
		        <div class="bubble-small" :style="random(85, 92)" v-for="(item, index) of bubble.small" :key="item + index">
		            <img v-lazy="require('@/assets/bubble.png')">
		            <span>{{ item }}</span>
		        </div>
		    </div>
		    <div v-if="isBroken && withdrawable">
		        <self-ball :amount="redress" :title="`${$t('message.currentCanDraw')}/SFC`"></self-ball>
		    </div>
		</div>
		<notice-bar
		  v-if="$i18n.locale === 'zh-CN'"
		  text="SmurfSFCimTokenimTokenLogoSFC"
		  color="#8D91C6"
		  font-size="24px"
		  background="#0C1748"
		  left-icon="volume-o"
		  @click="isNotice = true"
		></notice-bar>
		<notice-bar
		  v-else
		  text="The Smurf team is perfecting the submission of SFC's token-related data in the imToken wallet. After completion, you can see the Logo in the imToken wallet to make it easier to manage the SFC and see more token information."
		  color="#8D91C6"
		  font-size="24px"
		  background="#0C1748"
		  left-icon="volume-o"
		  @click="isNotice = true"
		></notice-bar>
		<div class="notice" v-if="$i18n.locale === 'zh-CN'" v-show="isNotice">
		    <icon name="cross" @click="isNotice = false" />
		    <steps
		      direction="vertical"
		      style="background-color: transparent; text-align: left;"
		      active-icon="stop"
		      inactive-icon="stop"
		    >
				<step>
		            <p class="step1">2020/06/01 18:00</p>
		            <h3 class="step2">Smurf</h3>
					<p class="team"></p>
		            <p class="step3">SmurfSmurf95%5%Smurf</p>
		        </step>
		        <step>
		            <p class="step1">2020/03/17 16:00</p>
		            <h3 class="step2">SFCimToken</h3>
		            <p class="step3">SmurfSFCimTokenimTokenLogoSFC</p>
		        </step>
		        <step>
		            <p class="step1">2020/03/10 16:00</p>
		            <h3 class="step2">imTokenSFC</h3>
		            <p class="step3">SFCSmurf ERC20Smurf DAPPimToken</p>
		        </step>
		    </steps>
		</div>
		<div class="notice" v-else v-show="isNotice">
		    <icon name="cross" @click="isNotice = false" />
		    <steps
		      direction="vertical"
		      style="background-color: transparent; text-align: left;"
		      active-icon="stop"
		      inactive-icon="stop"
		    >
				<step>
		            <p class="step1">2020/06/01 18:00</p>
		            <h3 class="step2">Announcement on the launch of the full operation phase of the Smurf system</h3>
					<p class="team">Smurf league team</p>
		            <p class="step3">Dear Smurf players, hello! Smurf is an open financial ecosystem. The performance of the system is very stable through the trial operation stage, which is deeply loved by players. Now officially into the full operation phase! Pool contract closed for the day! 95% of the money goes into the pool and 5% goes into the future pool. Smurf is committed to building an open financial ecosystem that is fair, just, open and entertaining.</p>
		        </step>
		        <step>
		            <p class="step1">2020/03/17 16:00</p>
		            <h3 class="step2">Announcement on improving the submission of SFC tokens to imToken wallet</h3>
		            <p class="step3">The Smurf team is perfecting the submission of SFC's token-related data in the imToken wallet. After completion, you can see the Logo in the imToken wallet to make it easier to manage the SFC and see more token information.</p>
		        </step>
		        <step>
		            <p class="step1">2020/03/10 16:00</p>
		            <h3 class="step2">Announcement on submitting SFC token information in imToken wallet</h3>
		            <p class="step3">SFC is the ERC20 token before the main line of the Smurf blockchain system is launched. It is the fuel for the circulation and Smurf DAPP system. The token information is now officially submitted to the imToken wallet.</p>
		        </step>
		    </steps>
		</div>
		<div class="button" v-if="isBroken">
			<Button @click="drawRedress" :disabled="!withdrawable" color="linear-gradient(#814ABC, #4C44D8)">
				{{ withdrawable ? $t('message.drawRemedy') : $t('message.drawRemedyDefine') }}
			</Button>
		</div>
		<div class="btn btn-path" v-else-if="isPause && isBind">
			<router-link :to="path">{{ $t('message.join') }}</router-link>
			<i></i>
			<router-link to="/income">{{ $t('message.draw') }}</router-link>
		</div>
		<div class="btn btn-span" @click="showBind = true" v-else-if="isPause && !isBind">
			<span>{{ $t('message.join') }}</span>
			<i></i>
			<span>{{ $t('message.draw') }}</span>
		</div>
		<div class="button" v-else>
			<Button disabled style="border: none;">{{ $t('message.income') }}</Button>
		</div>
		<span class="title">{{ $t('message.statistics') }}</span>
		<div class="carousel">
		    <div class="swiper-container">
		        <div class="swiper-wrapper">
		            <div class="swiper-slide">
		                <div class="funds">
		                    <img src="@/assets/funds.png" />
		                </div>
		                <span>{{ $t('message.capitalPool') }}</span>
		                <span>{{ amountPool }}</span>
		            </div>
		            <div class="swiper-slide">
		                <div class="jackpot">
		                    <img src="@/assets/jackpot.png" />
		                </div>
		                <span>{{ $t('message.bigPool') }}</span>
		                <span>{{ luckAssetsPoolA }}</span>
		            </div>
		            <!-- <div class="swiper-slide">
		                <div class="small_prize">
		                    <img src="@/assets/small_prize.png" />
		                </div>
		                <span>{{ $t('message.smallPool') }}</span>
		                <span>{{ luckAssetsPoolB }}</span>
		            </div> -->
		            <div class="swiper-slide">
		                <div class="total_burn">
		                    <img src="@/assets/total_burn.png" />
		                </div>
		                <span>{{ $t('message.totalBurn') }}</span>
		                <span>{{ erc20token }}</span>
		            </div>
		        </div>
		        <!-- <div class="swiper-pagination"></div> -->
		    </div>
		</div>
		<span class="title">{{ $t('message.luckTime') }}</span>
		<div class="countdown">
		    <count-down :time="time">
		        <template v-slot="timeData">
		            <span class="item">{{ timeData.days < 10 ? 0 : `${timeData.days}`[0] }}</span>
		            <span
		                class="item"
		            >{{ timeData.days < 10 ? timeData.days : `${timeData.days}`[1] }}</span>
		            <span class="spl">:</span>
		            <span class="item">{{ timeData.hours < 10 ? 0 : `${timeData.hours}`[0] }}</span>
		            <span
		                class="item"
		            >{{ timeData.hours < 10 ? timeData.hours : `${timeData.hours}`[1] }}</span>
		            <span class="spl">:</span>
		            <span class="item">{{ timeData.minutes < 10 ? 0 : `${timeData.minutes}`[0] }}</span>
		            <span
		                class="item"
		            >{{ timeData.minutes < 10 ? timeData.minutes : `${timeData.minutes}`[1] }}</span>
		            <span class="spl">:</span>
		            <span class="item">{{ timeData.seconds < 10 ? 0 : `${timeData.seconds}`[0] }}</span>
		            <span
		                class="item"
		            >{{ timeData.seconds < 10 ? timeData.seconds : `${timeData.seconds}`[1] }}</span>
		        </template>
		    </count-down>
		</div>
		<div class="recordTitle">
		    <!-- <span :class="isLatestJoin ? 'active' : ''" @click="isLatestJoin = true">{{ $t('message.newJoin') }}</span> -->
		    <span class="active">{{ $t('message.newJoin') }}</span>
		    <!-- <span :class="isLatestJoin ? '' : 'active'" @click="isLatestJoin = false">{{ $t('message.luckPlayer') }}</span> -->
		</div>
		<div class="record">
		    <div class="record-header">
		        <img v-lazy="require('@/assets/record_logo.png')" />
		    </div>
		    <!-- <div class="record-content" v-show="isLatestJoin"> -->
		    <div class="record-content">
		        <div v-for="(item, index) of newJoin" :key="index">
		            <span>{{ `${item.address.slice(0, 10)}****${item.address.slice(-10)}` }}</span>
		            <span>{{ item.amount }}ETH</span>
					<!-- <span>{{ item.time }}</span> -->
		        </div>
		    </div>
			<!-- <div class="record-content" v-show="!isLatestJoin">
			    <div v-for="(item, index) of luckPlayer" :key="index">
			        <span>{{ `${item.address.slice(0, 6)}****${item.address.slice(-6)}` }}</span>
			        <span>{{ item.amount }}ETH</span>
					<span>{{ item.time }}</span>
			    </div>
			</div> -->
		</div>
		<self-tabbar active="home"></self-tabbar>
		<overlay class="luck" :show="isLuck" @click="isLuck = false">
		    <div @click.stop>
		        <div v-lazy:background="require('@/assets/luck.png')">
					<span>{{ $t('message.lucks') }}</span>
		            <span>{{ luckAmount }}</span>
					<span>ETH</span>
		        </div>
		        <Button @click="getLuck" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.draw') }}</Button>
		    </div>
		</overlay>
		<Overlay class="upgrade" :show="isUpgrade" @click="isUpgrade = false">
		    <div v-lazy:background="require('@/assets/luck.png')">
				<span>{{ $t('message.upgradeText') }}</span>
		        <span>V{{ grade }}</span>
		    </div>
		    <Button @click="upgrade" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.upgrade') }}</Button>
		</Overlay>
		<overlay class="bind" :show="showBind" @click="showBind = false">
		    <div @click.stop>
		        <div>
		            <span>{{ $t('message.welcome') }}</span>
		            <icon class="icon" name="cross" @click="showBind = false" />
		        </div>
		        <input type="text" v-model="bindCode" autofocus :placeholder="$t('message.bind')" />
		        <input type="text" v-model="generateCode" :placeholder="$t('message.createCode')" />
		    </div>
			<Button @click.stop="bind" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.define') }}</Button>
		</overlay>
		<not-wallet :isWallet="isWallet"></not-wallet>
	</div>
</template>

<script>
	import selfTabbar from '@/components/self-tabbar'
	import { NoticeBar, CountDown, Overlay, Button, Icon, Step, Steps, Toast, CellGroup } from 'vant'
	import NotWallet from '@/components/not-wallet'
	import SelfBall from '@/components/self-ball'
	import Swiper from "swiper"
	import "swiper/css/swiper.min.css"
	import { isWallet } from '@/static/initData.js'

	export default {
		name: "home",
		components: {
			selfTabbar,
			NoticeBar,
			CountDown,
			Overlay,
			NotWallet,
			Button,
			Icon,
			Step,
			Steps,
			SelfBall
		},
		data() {
			return {
				isWallet: !isWallet,
				isBind: localStorage.getItem('isBind') === 'true',
				showBind: false,
				energy: 0,
				totalIncome: '0.00',
				outAmount: '0.00',
				withdrawable: false,
				isBroken: false,
				isPause: false,
				redress: '0.00',
				bubble: { big: [], middle: [], small: [] },
				path: '/join',
				amountPool: "0.00",
				luckAssetsPoolA: "0.00",
				// luckAssetsPoolB: "0.00",
				erc20token: "0.00",
				time: 0,
				newJoin: [],
				// luckPlayer: [],
				// isLatestJoin: true,
				isLang: false,
				isNotice: false,
				bindCode: '',
				generateCode: '',
				luckAmount: '0.00',
				isLuck: false,
				isUpgrade: false,
				grade: 0
			}
		},
		methods: {
			init() {
				if (this.$cache.home) {
				    this.energy = this.$cache.home.energy
					this.totalIncome = this.$cache.home.totalIncome
					this.outAmount = this.$cache.home.outAmount
					this.withdrawable = this.$cache.home.withdrawable
					this.isBroken = this.$cache.home.isBroken
					this.isPause = this.$cache.home.isPause
					this.redress = this.$cache.home.redress
					this.bubble = this.$cache.home.bubble
					this.path = this.$cache.home.path
					this.amountPool = this.$cache.home.amountPool
					this.luckAssetsPoolA = this.$cache.home.luckAssetsPoolA
					// this.luckAssetsPoolB = this.$cache.home.luckAssetsPoolB
					this.erc20token = this.$cache.home.erc20token
					this.time = this.$cache.home.time
					this.newJoin = this.$cache.home.newJoin
					this.luckPlayer = this.$cache.home.luckPlayer
				} else {
				    this.$cache.home = {}
				}
				
				this.getData()
			},
			async getData() {
				this.$contract.turing.methods.GetSafetyValue().call((err, res) => {
				    if (!err) {
				        this.energy = Number(res)
				        this.$cache.home.energy = Number(res)
				    }
				})
				
				this.$contract.round.methods.TotalInOutAmount()
				.call({ from: this.$address }, (err, res) => {
				    if (!err) {
				        this.totalIncome = this.$fromWei(res.outEther, 2)
				        this.$cache.home.totalIncome = this.$fromWei(res.outEther, 2)
				    }
				})
				
				this.$contract.round.methods.GetCurrentGameStatus()
				.call({ from: this.$address }, (err, res) => {
				    if (!err) {
				        let outAmount = this.$fromWei(res.currentEther) * Number(res.profixMultiplier) - this.$fromWei(res.withdrawableTotal)
				        this.outAmount = outAmount.toFixed(2)
				        this.$cache.home.outAmount = outAmount.toFixed(2)
				        this.path = res.createTime === "0" ? "/join" : "/againJoin"
				        this.$cache.home.path = this.path
				    }
				})
				
				this.$contract.round.methods.GetRedressInfo()
				.call({ from: this.$address }, (err, res) => {
				    if (!err) {
				        this.redress = this.$fromWei(res.total)
				        this.withdrawable = res.withdrawable
				
				        this.$cache.home.redress = this.redress
				        this.$cache.home.withdrawable = this.withdrawable
				    }
				})
				
				this.$contract.round.methods.isBroken().call((err, res) => {
				    if (!err) {
				        this.isBroken = res
				        this.$cache.home.isBroken = res
				    }
				})
				
				this.$contract.luckAssetsPoolA.methods._needPauseGame().call((err, res) => {
				    if (!err) {
				        this.isPause = !res
				        this.$cache.home.isPause = !res
				    }
				})
				
				this.$contract.round.methods.CurrentCanWithdrawProfix(this.$address).call((err, res) => {
				    if (!err) {
				        let st = this.$fromWei(res.st),
				            dy = this.$fromWei(res.dy),
				            canWithdraw = st + dy
				
				        if (canWithdraw > Number(this.outAmount)) {
				            canWithdraw = Number(this.outAmount)
				        }
				
				        this.bubble = this.initBubble(canWithdraw, { big: [], middle: [], small: [] })
				        this.$cache.home.bubble = this.bubble
				    }
				})
				
				this.$contract.recommend.methods.GetIntroducer(this.$address)
				.call((err, res) => {
				    if (!err) {
						this.isBind = Boolean(Number(res))
						localStorage.setItem('isBind', `${this.isBind}`)
				    }
				})

				Promise.all([
					this.$web3.eth.getBalance(this.$contract.round._address),
					this.$web3.eth.getBalance(this.$contract.luckAssetsPoolA._address),
					// this.$web3.eth.getBalance(this.$contract.luckAssetsPoolB._address),
					this.$contract.erc20token.methods.balanceOf("0x0000000000000000000000000000000000000000").call()
				]).then(res => {
					this.amountPool = this.$fromWei(res[0], 2)
					this.luckAssetsPoolA = this.$fromWei(res[1], 2)
					// this.luckAssetsPoolB = this.$fromWei(res[2], 2)
					this.erc20token = parseInt(this.$fromWei(res[2]))

					this.$cache.home.amountPool = this.amountPool
					this.$cache.home.luckAssetsPoolA = this.luckAssetsPoolA
					// this.$cache.home.luckAssetsPoolB = this.luckAssetsPoolB
					this.$cache.home.erc20token = this.erc20token

					new Swiper(".swiper-container", {
				        slidesPerView: 3,
				        spaceBetween: 10,
				        // loop: false,
				        // pagination: {
				        //   el: '.swiper-pagination'
				        // }
				    })
				}).catch(e => {
					this.$cache.home.amountPool = '0.00'
					this.$cache.home.luckAssetsPoolA = '0.00'
					// this.$cache.home.luckAssetsPoolB = '0.00'
					this.$cache.home.erc20token = '0.00'

					new Swiper(".swiper-container", {
				        slidesPerView: 3,
				        spaceBetween: 10,
				        // loop: false,
				        // pagination: {
				        //   el: '.swiper-pagination'
				        // }
				    })
				})
				
				this.$contract.luckAssetsPoolA.methods._lotteryTime().call((err, res) => {
				    if (!err) {
				        this.time = new Date(Number(res) * 1000).getTime() - new Date().getTime()
				        this.$cache.home.time = this.time
				    }
				})
				
				this.$contract.luckAssetsPoolA.methods.RewardsAmount()
				.call({ from: this.$address }, (err, res) => {
				    if (!err) {
				        this.luckAmount = this.$fromWei(res)
				        this.isLuck = this.luckAmount > 0
				    }
				})
				
				this.$contract.levelSub.methods.LevelOf(this.$address).call((err, res) => {
				    if (!err) {
				        this.$contract.levelSub.methods.CanUpgradeLv(this.$address).call((error, result) => {
				            if (!err && Number(result) > 0 && Number(res) < 4) {
				                this.isUpgrade = true
				                this.grade = Number(result)
				            }
				        })
				    }
				})
				
				this.$http.getNewJoin().then(res => {
					this.newJoin = []
					res.forEach(item => {
						let address = this.$web3.eth.abi.decodeParameter('address', item.topics[1]),
						// 	timestamp = this.$web3.eth.abi.decodeParameter('uint256', item.topics[2]),
							amount = this.$web3.eth.abi.decodeParameter('uint256', item.topics[3])
						// 	date = new Date(Number(timestamp) * 1000),
						// 	year = date.getFullYear(),
						// 	month = date.getMonth() + 1,
						// 	day = date.getDate()
							
						this.newJoin.unshift({
							address: address,
							amount: this.$fromWei(amount, 2)
							// time: `${year}.${month}.${day}`
						})
					})
					
					this.$cache.home.newJoin = this.newJoin
				})
				
				// this.$http.getLuckPlayer().then(res => {
				// 	this.luckPlayer = []
				// 	res.forEach(item => {
				// 		let data = this.$web3.eth.abi.decodeParameters(['address', 'uint256', 'uint256'], item.data),
				// 			date = new Date(Number(data[1]) * 1000),
				// 			year = date.getFullYear(),
				// 			month = date.getMonth() + 1,
				// 			day = date.getDate()
							
				// 		this.luckPlayer.unshift({
				// 			address: data[0],
				// 			amount: this.$fromWei(data[2], 2),
				// 			time: `${year}.${month}.${day}`
				// 		})
				// 	})
					
				// 	this.$cache.home.luckPlayer = this.luckPlayer
				// })
			},
			drawRedress() {
			    this.$contract.round.methods.DrawRedress().send({
			        from: this.$address
			    }).on("error", console.error)
			    .on("receipt", res => {
			        this.withdrawable = false
			        this.getData()
			    })
			},
			changeLanguage(lang) {
			    this.$i18n.locale = lang
			    localStorage.setItem('language', lang)
			},
			upgrade() {
			    this.$contract.levelSub.methods.DoUpgradeLv().send({
			        from: this.$address
			    }).on("transactionHash", txHash => {
			        console.log(txHash)
			    }).on("error", console.error)
			    .on("receipt", res => {
			        this.isUpgrade = false
			        this.getData()
			    })
			},
			getLuck() {
			    this.$contract.luckAssetsPoolA.methods.WithdrawRewards().send({
			        from: this.$address
			    }).on("error", console.error)
			    .on("receipt", res => {
			        this.isLuck = false
			        this.getData()
			    })
			},
			async bind() {
			    if (this.bindCode.length < 6) {
			        return Toast.fail(this.$t('message.errBindLen'))
			    } else if (this.generateCode.length !== 6) {
			        return Toast.fail(this.$t('message.errCodeLen'))
			    }
			
			    let bindCode = this.$web3.utils.stringToHex(this.bindCode.toUpperCase()),
			        generateCode = this.$web3.utils.stringToHex(this.generateCode.toUpperCase()),
			        toAddress = await this.$contract.recommend.methods.ShortCodeToAddress(bindCode).call(),
			        thisAddress = await this.$contract.recommend.methods.ShortCodeToAddress(generateCode).call()
			
			    if (!parseInt(toAddress, 16)) {
			        return Toast.fail(this.$t('message.errBind'))
			    } else if (parseInt(thisAddress, 16)) {
			        return Toast.fail(this.$t('message.errCode'))
			    }
			
			    this.$contract.round.methods.Activity(toAddress, generateCode).send({
			        from: this.$address
			    }).on("error", console.error)
			    .on("receipt", res => {
			        this.isBind = true
					this.showBind = false
			        localStorage.setItem('isBind', 'true')
					this.$router.push({ path: '/' })
			    })
			},
			random(top, left) {
			    return {
			        top: `${Math.random() * top}%`,
			        left: `${Math.random() * left}%`
			    }
			},
			initBubble(income, obj) {
			    let ten = Math.floor(income / 10),
			        five = Math.floor((income - ten * 10) / 5),
			        one = Math.floor((income - ten * 10 - five * 5) / 1)
			
			    for (let i = 0; i < ten; i++) {
			        obj.big.push('10.00')
			    }
			    for (let i = 0; i < five; i++) {
			        obj.big.push('5.00')
			    }
			    for (let i = 0; i < one; i++) {
			        obj.big.push('1.00')
			    }
			
			    return this.middleBubble((income - ten * 10 - five * 5 - one * 1), obj)
			},
			middleBubble(income, obj) {
			    let middle1 = Math.floor(income / 0.5),
			        middle2 = Math.floor((income - middle1 * 0.5) / 0.1)
			
			    for (let i = 0; i < middle1; i++) {
			        obj.middle.push(0.5)
			    }
			    for (let i = 0; i < middle2; i++) {
			        obj.middle.push(0.1)
			    }
			    
			    return this.smallBubble((income - middle1 * 0.5 - middle2 * 0.1), obj)
			},
			smallBubble(income, obj) {
			    let small1 = Math.floor(income / 0.05),
			        small2 = Math.floor((income - small1 * 0.05) / 0.02),
			        small3 = Math.floor((income - small1 * 0.05 - small2 * 0.02) / 0.01)
			
			    for (let i = 0; i < small1; i++) {
			        obj.small.push(0.05)
			    }
			    for (let i = 0; i < small2; i++) {
			        obj.small.push(0.02)
			    }
			    for (let i = 0; i < small3; i++) {
			        obj.small.push(0.01)
			    }
			    
			    return obj
			}
		},
		mounted() {
			if (this.$route.query.bind) {
				this.showBind = true
			}
			
			if (isWallet) {
				// let userAgent = navigator.userAgent,
				//     isIOS = !!userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
				
				// if (isIOS) {
				//     let pagination = document.querySelector('.swiper-pagination'),
				//         title = document.querySelector('.countdown')
				    
				//     pagination.style.bottom = '-1.5rem'
				//     title.style.marginTop = '.3rem'
				// }
				
			    this.init()
			}
		}
	};
</script>

